import React from 'react'
import { useSelector } from 'react-redux'
import { Image, Card } from 'semantic-ui-react'
import moment from 'moment'
import { isMobile } from 'react-device-detect'

const News1 = () => {
  const countryNews = useSelector((state) => state.country.news.articles)

  const regx = /\[\+\d+\schars\]/g

  const news2 = countryNews.map((item) => {
    return { ...item, content: item.content.replace(regx, '') }
  })

  return (
    <>
      {' '}
      {isMobile ? <></> : <Card 
      id='card' 
      fluid 
      content="News obtained from newsapi.org. Relevance to selected country is not guaranteed." />}
      <Card.Group 
   //   id='card'  
      itemsPerRow={isMobile ? 1 : 3}>
        {isMobile ? <Card id='card-mobile' fluid content="News obtained from newsapi.org. Relevance to selected country is not guaranteed." /> : <></>}
        {news2?.map((item, i) => {
          console.log('item', item)
          const date = moment(item.publishedAt).format('YYYY-MM-DD LT')
          return (
            <Card raised as="a" href={item.url} key={i}>
              <Image src={item.urlToImage} />
              <Card.Content>
                <Card.Header>{item.title} </Card.Header>
                <Card.Meta>{date} </Card.Meta>
                {item.author ? (
                  <Card.Meta>Author: {item.author} </Card.Meta>
                ) : (
                  <></>
                )}
                <Card.Meta>Source: {item.source.name} </Card.Meta>
                <Card.Description>{item.content} </Card.Description>
              </Card.Content>
            </Card>
          )
        })}
      </Card.Group>
    </>
  )
}

const News = React.memo(News1)

export default News
